#more-velocita{
    position: relative;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    
    .main-container{
        position: relative;
        background-color: #2a2a2a;
        width: 100%;
        height: 150px;
        display: flex;
        align-items: center;
        justify-content: center;

        .banner{
            height: 170px;
            width: calc(100% - 120px);
            background-color: #92929221;
            position: absolute;
            transform: translateY(80px);
            border-radius: 8px;
            box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
            backdrop-filter: blur(10.4px);
            -webkit-backdrop-filter: blur(15.4px);
            border: 1px solid rgba(26, 26, 26, 0.35);
        
            padding: 0 60px;
            display: flex;
            align-items: center;
            justify-content: center;

            
            .text-color{
                color: tomato;
            }

            .title{
                color: rgb(180, 180, 180);
                text-align: center;
            }

            .sub-title{
                padding-left: 70px;
                color: rgb(180, 180, 180);

                @media screen and (max-width:450px) {
                    padding-left: 0;
                    margin-top: 20px;
                    text-align: center;
                  }
            }


            @media screen and (max-width:450px) {
                width: calc(100% - 60px);
                flex-direction: column;
                padding: 0 30px;
                transform: translateY(40px);
                height: 220px;
            }
        }

        @media screen and (max-width:450px) {
          height: 220px;
        }
    }
}