#section-one-checkout {
  min-height: 100vh;
  padding: 150px 0;
  display: flex;
  align-items: center;
  justify-content: center;

  .styled-character-position {
    top: 35px;
    right: 50%;
  }

  .sub-info {
    position: absolute;
    top: 40px;
    left: 40px;
    @media screen and (max-width: 450px) {
      top: 30px;
      left: 30px;
    }

    button {
      // border-radius: 100%;
      border: 1px solid tomato;
      padding: 0;

      .icon {
        color: #000;
        font-size: 3rem;
        padding: 10px;
        @media screen and (max-width: 450px) {
          font-size: 2rem;
        }
      }
    }
  }

  .responsive-image {
    width: 600px;
    border: 1px solid #000;
  }
  @media screen and (max-width: 600px) {
    .responsive-image {
      width: 100%;
      border: 1px solid #000;
    }
  }

  .main-container {
    width: 100%;
    height: 100%;
    color: #505050;

    // min-height: 100vh;

    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;

    .underline {
      height: 0.15rem;
      width: 60px;
      background-color: tomato;
      margin: 5px 0;
    }

    .underline-small {
      height: 0.12rem;
      width: 30px;
      background-color: tomato;
      margin: 5px 0;
    }

    .left {
      height: 100%;
      width: 50%;
      position: relative;
      padding: 0 60px;

      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: center;

      .section-subtitle {
        margin-top: 10px;
        margin-bottom: 10px;
        font-weight: 700;
        font-size: 1.1rem;
        line-height: 1.6rem;
        @media screen and (max-width: 420px) {
          font-size: 0.95rem;
          line-height: 1.6rem;
        }

        @media screen and (max-width: 360px) {
          font-size: 0.85rem;
          line-height: 1.4rem;
        }
      }

      .section-title {
        // text-transform: capitalize;
        font-size: 3.2em;

        @media screen and (max-width: 420px) {
          font-size: 2rem;
        }

        @media screen and (max-width: 360px) {
          font-size: 1.8rem;
        }
        .change-color {
          text-transform: capitalize;
          font-size: 5rem;
          color: tomato;

          // color: rgb(255, 208, 0);
          // font-family: 'Roboto', sans-serif;

          @media screen and (max-width: 420px) {
            font-size: 3rem;
          }

          @media screen and (max-width: 360px) {
            font-size: 3.6rem;
          }
        }
      }

      .get-started {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-top: 10px;

        .arrow {
          color: tomato;
          margin-left: 10px;
        }
      }

      .info-box {
        bottom: 0px;
        left: 0px;
        height: 115px;
        width: 95px;
        position: absolute;
        background-color: unset;

        @media screen and (max-width: 420px) {
          height: 100px;
        }

        .box-left {
          left: 60px;
          width: 225px;
          color: #000;

          @media screen and (max-width: 420px) {
            left: 30px;
            width: 200px;
          }
        }

        .box-right {
          right: -250px;
          @media screen and (max-width: 420px) {
            right: -200px;
          }
        }

        .arrow {
          font-size: 2.8rem;
          padding: 8px;
          border: 0.15rem solid tomato;
          border-radius: 100%;
        }
      }
    }

    .right {
      width: 50%;
      padding: 0 60px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;

      .link-block {
        position: relative;
        margin: 15px 0;
        width: 345px;
        background-color: #1a1a1a00;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .in-page-link {
          display: flex;
          align-items: center;
          justify-content: space-between;
          text-decoration: none;
          color: #000;
          width: 100%;
        }

        .block {
          position: absolute;
          left: 0;
          height: 100%;
          width: 2px;
          background-color: tomato;

          @media screen and (max-width: 420px) {
            left: 15px;
          }
        }

        .block-info {
          padding-left: 30px;

          display: flex;
          flex-direction: column;
          align-items: flex-start;
          justify-content: center;

          .title {
            letter-spacing: 0.1rem;
          }

          .sub-title {
            padding-bottom: 3px;
          }
        }
        

        .arrow {
          margin-left: 15px;
          border-radius: 100%;
          padding: 8px;
          font-size: 2.2rem;
          border: 0.15rem solid tomato;
        }
        @media screen and (max-width: 420px) {
          padding: 0 15px;
          width: 100%;
        }
        @media screen and (max-width: 390px) {
          // width: 275px;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  #section-one-checkout {
    // min-height: calc(100vh + 250px);

    display: flex;
    align-items: center;
    justify-content: center;

    .main-container {
      width: 100%;
      height: 100%;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;

      .left {
        width: 100%;
        align-items: flex-start;
        justify-content: center;
        padding: 0px 30px 0 30px;
      }
      .right {
        width: 100%;
        padding: 50px 30px;
      }
    }
  }
}
