// Section One/1 | Landing Page | Home Page

#section-one {
  height: 850px;
  // background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;

  @media screen and (max-width: 1920px) {
    height: 100vh;
  }

  .main-container {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--primaryColorDarkGrey);

    .sub-container {
      height: 100%;
      width: 50%;
    }

    .left {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      padding: 0px 60px;
      color: var(--offWhiteColor);

      @media screen and (max-width: 420px) {
        padding: 0 30px;
      }

      .div-box-two {
        height: 300px;
        width: 5px;
        background-color: tomato;

        position: absolute;
        left: 0;
      }

      .main-heading {
        text-transform: capitalize;
      }

      .sub-heading {
        margin: 15px 0 0 0;

        // display: flex;
        // align-items: center;
        // justify-content: center;
        line-height: 1.6rem;
        font-weight: 700;
        font-size: 1rem;
        // letter-spacing: .1rem;

        @media screen and (max-width: 420px) {
          font-size: 0.85rem;
          line-height: 1.4rem;
        }
        @media screen and (max-width: 390px) {
          font-size: 0.75rem;
        }

        @media screen and (max-width: 360px) {
          font-size: 0.65rem;
        }

        .arrow {
          transform: rotateZ(180deg);
          height: 20px;
          margin-right: 15px;
          @media screen and (max-width: 420px) {
            height: 30px;
          }
        }
      }

      .styled-character-position {
        top: 35px;
        right: 0px;
      }

      .info-box {
        bottom: 35px;
        left: 0px;
        height: 115px;
        width: 95px;
        position: absolute;
        background-color: var(--secondaryColor);

        .box-left {
          left: 60px;
          width: 225px;
        }

        .box-right {
          right: -250px;
        }

        .arrow {
          font-size: 2.8rem;
          padding: 8px;
          border: 0.15rem solid tomato;
          border-radius: 100%;
        }
      }
    }

    .right {
      background-color: var(--secondaryColor);
      position: relative;
      display: flex;
      align-items: center;
      justify-content: flex-start;

      // background-image: url(../../../assets/Banner/landing-page.png);
      // background-repeat: no-repeat;
      // background-position: center;
      // background-size: cover;

      .vrf-image-container {
        height: 80%;
        position: absolute;
        bottom: 0;
        left: -50px;
        width: 100%;

            
        @media screen and (max-width: 768px) {
          height: 70%;
          left: 0;
          top: 0;
          width: 100%;
        }

        .box-heading {
          position: absolute;
          z-index: 1;
          width: auto;
          display: flex;
          align-items: center;
          justify-content: flex-start;
          padding: 30px;
          text-transform: uppercase;
          font-size: 0.9rem;
          color: #000000;
          background-color: rgba(255, 255, 255, 0.829);

          bottom: 0;
          @media screen and (max-width: 450px) {
            padding: 20px 30px;
          }
          .arrow {
            color: #f80;
            font-size: 1rem;
            margin-right: 15px;
          }
        }

        .box-footer {
          position: absolute;
          top: 0;
          z-index: 1;
          padding: 30px;
          background-color: rgba(255, 255, 255, 0.753);
          @media screen and (max-width: 450px) {
            padding: 20px 30px;
          }
          .large {
            font-size: 3.6rem;
            font-weight: 700;
            color: #f80;

            @media screen and (max-width: 450px) {
              font-size: 2.8rem;
            }

            @media screen and (max-width: 375px) {
              font-size: 2rem;
            }

            .super {
              font-size: 1.5rem;
            }
          }
        }
      }

      .vrf-image {
        position: absolute;
        // height: calc(100% - 70px);
        height: 100%;
        // width: 100%;
        bottom: 0;
        left: 0;
        // right: 0;
        // display: none;
        // box-shadow: 0 4px 8px 0 #000000c2;


        @media screen and (max-width: 810px) {
          height: 100%;
          max-width: 100%;
        }

        @media screen and (max-width: 420px) {
          height: 100%;
          max-width: 100%;
        }
        @media screen and (max-width: 390px) {
          // height: 160px;
        }

        @media screen and (max-width: 360px) {
          // height: 140px;
        }
      }

      .info-box {
        width: 400px;
        height: 115px;
        background-color: var(--primaryColorDarkGrey);
        bottom: 35px;
        right: 0px;
        position: absolute;

        // @media screen and (max-width: 450px) {
        //   width: 300px;
        // }

        @media screen and (max-width: 420px) {
          width: 260px;
        }
        @media screen and (max-width: 360px) {
          width: 250px;
        }

        .box-left {
          left: 45px;
          width: 225px;

          @media screen and (max-width: 768px) {
            // left: -30px;
          }
          @media screen and (max-width: 420px) {
            left: -60px;
          }
          @media screen and (max-width: 390px) {
            left: -75px;
            // width: 200px;
          }
          @media screen and (max-width: 360px) {
            left: -45px;
            width: 200px;
          }
        }

        .box-right {
          right: 60px;

          @media screen and (max-width: 768px) {
            right: 60px;
          }
          @media screen and (max-width: 420px) {
            right: 30px;
          }
          @media screen and (max-width: 390px) {
            right: 30px;
          }
          @media screen and (max-width: 360px) {
            right: 30px;
          }
        }

        .arrow {
          font-size: 2.8rem;
          padding: 8px;
          border: 0.15rem solid tomato;
          border-radius: 100%;
          color: var(--fontprimarycoloroffWhite);
        }

        .no-display {
          display: none;
        }
      }

      .styled-character-position {
        right: 30px;
        bottom: 50%;
        transform: rotateZ(-90deg);
      }
    }
  }
}

// 768 Breakpoint

@media screen and (max-width:1098px) {
  #section-one {
    height: unset;

    .main-container {
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      .sub-container {
        height: 100vh;
        width: 100%;
      }
    }
  }
}
