#sec-main {
  .text {
    margin-top: 20px;
  }

  .func-list {
    padding-left: 30px;
  }

  .form-main {
    margin: unset;
  }

  .search-button{
  
  }

  .right-container, .loader, .table-container{
    margin-top: 100px;
  }

  .inv-form {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
    width: 60%;

    .text-box {
      margin: unset;
      margin-right: 30px;
      margin-bottom: 30px;
    }

    .x-small {
      width: 20%;
    }

    .small {
      width: 25%;
    }

    .large {
      width: 75%;
    }

    .x-large {
      width: 100%;
    }
  }
}


@media screen and (max-width:600px) {
  #sec-main {
    .inv-form {
      margin-top: 50px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      flex-wrap: wrap;
      width: 100%;
  
      .text-box {
        margin: unset;
        margin-right: 30px;
        margin-bottom: 30px;
      }
  
      .x-small {
        width: 80%;
      }
  
      .small {
        width: 100%;
      }
  
      .large {
        width: 100%;
      }
  
      .x-large {
        width: 100%;
      }
    }
  }
}