.payment-dialogue {
  .content {
    // min-height: 175px;

    .buttons {
      display: flex;
      align-items: center;
      justify-content: center;
      button {
        margin: 10px;
        background-color: #f3f3f3;
        .payment-image {
          height: 125px;

          @media screen and (max-width: 420px) {
            height: 90px;
          }
        }
      }
    }

    .text {
      margin-top: 15px;
    }
  }
