      }
    }

    .left {
      width: 50%;
      padding: 0 60px 0 60px;

      @media screen and (max-width: 768px) {
        width: 100%;
        padding: 0 30px 0 30px;
      }

      .section-subtitle {
        margin-top: 30px;
        margin-bottom: 10px;
        font-weight: 700;
        font-size: 0.95rem;
        line-height: 1.6rem;
        @media screen and (max-width: 420px) {
          font-size: 0.95rem;
          line-height: 1.6rem;
        }

        @media screen and (max-width: 360px) {
          font-size: 0.85rem;
          line-height: 1.4rem;
        }
      }

      .section-title {
        // text-transform: capitalize;
        font-size: 3.2em;

        @media screen and (max-width: 420px) {
          font-size: 3rem;
        }

        @media screen and (max-width: 360px) {
          font-size: 2.8rem;
        }
        .change-color {
          text-transform: capitalize;
          font-size: 4.6rem;
          color: tomato;
          // color: rgb(255, 208, 0);
          // font-family: 'Roboto', sans-serif;

          @media screen and (max-width: 420px) {
            font-size: 4rem;
          }

          @media screen and (max-width: 360px) {
            font-size: 3.8rem;
          }
        }
      }

      .container {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
      }

      .mob-image {
        display: none;
      }
      .mob-image-active {
        display: none;
      }

      @media screen and (max-width: 768px) {
        .mob-image-active {
          display: block;
          width: 100%;
        }
      }

      .card {
        // height: 100px;
        width: 90%;
        background-color: #1a1a1a00;
        box-shadow: unset;
        position: relative;
        margin: 15px 0;

        @media screen and (max-width: 768px) {
          width: 100%;
          margin: 10px 0;
        }

        @media screen and (max-width: 450px) {
          height: 110px;
        }

        @media screen and (max-width: 390px) {
          height: 80px;
        }

        .line {
          position: absolute;
          left: 0;
          height: 100%;
          width: 2px;
          background-color: rgb(73, 73, 73);

          .inner-line {
            background-color: tomato;
            height: 100%;
            width: 2px;

            transform: translateY(-150px);
          }
        }

        .card-area {
          width: 100%;
          height: 100%;
          padding: 10px 20px;

          display: flex;
          flex-direction: column;
          align-items: flex-start;
          justify-content: flex-start;

          .card-title {
            font-size: 1.4rem;
            color: #c2c1c1;

            @media screen and (max-width: 450px) {
              font-size: 1.2rem;
            }

            @media screen and (max-width: 390px) {
              font-size: 1rem;
            }
          }

          .card-sub-title {
            color: #c0bfbf;

            @media screen and (max-width: 450px) {
              font-size: 0.875rem;
            }

            @media screen and (max-width: 390px) {
              font-size: 0.675rem;
            }
          }
        }
      }

      .card-active {
        .line {
          overflow: hidden;
          .inner-line {
            transform: translateY(0px);
            transition: 0.25s ease-in 0s;
          }
        }
      }
    }

    .right {
      width: 50%;
      padding: 0 60px 0 0;

      .container {
        display: flex;
        align-items: center;
        justify-content: center;
        .image {
          display: none;
          // opacity: 0%;
          width: 100%;
          border-radius: 5px;
          // transition: .5s ease-in-out 0s;

          @media screen and (max-width: 768px) {
            display: none;
          }
        }

        .image-active {
          display: block;
          // opacity: 100%;
          width: 100%;
          border-radius: 5px;
          // transition: .5s ease-in-out 0s;
          @media screen and (max-width: 768px) {
            display: none;
          }
        }
      }
    }
  }
}

