.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 9999;
}
  
.popup-content {
  max-width: 80%;
  max-height: 100vh; /* Ensure it doesn't exceed screen height */
  overflow: auto;
  position: relative;
  z-index: 10000;
}
  
.popup-content img {
  max-width: 100%;
  max-height: 100vh; /* Restrict image height to viewport height */
  display: block;
  margin: auto;
  object-fit: contain;
}