#section-one-register {
  display: flex;
  align-items: center;
  justify-content: center;

  .header{
    position: absolute;
    top: 40px;
    left: 40px;
    z-index: 1;
    display: flex;
    // flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }

  .header-info{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    margin-left: 30px;

    .heading {
      margin-bottom: 5px;
      font-size: 1.2rem;

      @media screen and (max-width:470px) {
        font-size: .95rem;
      }

      @media screen and (max-width:370px) {
        font-size: .85rem;
      }
    }
  
    .sub-heading{
      color: #f80;
      @media screen and (max-width:470px) {
        font-size: .75rem;
        line-height: 1rem;
      }
    }
  }

  .sub-info {
    // position: absolute;
    // top: 40px;
    // left: 40px;
    z-index: 1;
    @media screen and (max-width: 450px) {
      top: 30px;
      left: 30px;
    }

    button {
      // border-radius: 100%;
      border: 1px solid tomato;
      padding: 0;

      .icon {
        color: #000;
        font-size: 3rem;
        padding: 10px;
        @media screen and (max-width: 450px) {
          font-size: 2rem;
        }
      }
    }
  }
  

  .main-container {
    width: 100%;
    // display: flex;
    // align-items: center;
    // justify-content: center;
    // padding: 150px 60px 150px 60px;
    // background-color: yellow;'
    // background-image: url(../../../assets/Banner/solid-works.png);
    // background-position: center;
    // background-repeat: no-repeat;
    // background-size: cover;
    position: relative;
    .form-container {
      display: flex;
      align-items: center;
      justify-content: center;
      min-height: 100vh;
      padding: 150px 60px 150px 60px;
      width: 70%;
      background-color: #ffff;
      // backdrop-filter: blur(15px);

      @media screen and (max-width: 420px) {
        width: 100%;
        padding: 150px 30px;
      }
    }

    .register-banner {
      position: absolute;
      right: 0;
      bottom: 0;
      // height: 300px;
      width: 30%;

      @media screen and (max-width: 420px) {
        display: none;
      }
    }

    .register-banner-cad {
      position: absolute;
      right: 0;
      bottom: 50%;
      // height: 300px;
      width: 30%;
    }
  }
}
