.feedback-form {
    position: relative;
    height: 100%;
    width: 100%;
  
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;

    .container{
      display: flex;
      flex-direction: column;
    }
  
    .form-control {
      position: relative;
      height: 100%;
      width: 100%;
      padding-bottom: 120px;
    }

    .buttons{
      margin-top: 30px;
    }
  
    .text-field {
      width: 300px;
      margin-top: 20px;
      // background-color: #f3f1f1;
      
  
      @media screen and (max-width:420px) {
          width: 100%;
      }
  
      label {
        color: #505050;
      }
  
      input {
        color: #505050;
      }
  
      textarea {
        color: #505050;
        
      }
    }
  
    .send-button {
      position: absolute;
      bottom: 0;
      right: 0;
      color: #505050;
      // border-color: #505050;
  
      .arrow {
        margin-left: 10px;
        color: tomato;
      }
    }
  
    .reset-button {
      position: absolute;
      left: 0;
      bottom: 0;
      // color: #505050;
    }
  }

  .questions-container{
    margin-top: 20px;
   
    
    .questions{
      margin-top: 20px;

      .question-text{
        margin: 15px 0;
      }
    }

  }
  
  
  #success-form{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: relative;
      height: 100%;
      width: 100%;
  
      .form-reset-button{
          position: absolute;
          bottom: 0;
          right: 0;
          // color: #505050;
      }
  }